.button {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 8px 44px;
  text-decoration: none;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  border: none;
  background-color: $bg-button-basic;
  color: $text-basic-light;
  margin: 0;
  box-sizing: border-box;
  text-align: center;
}


.button--primary {
  font-size: 16px;
  line-height: 20px;
  padding: 10px 0;
}

.button--secondary {
  color: $text-grey400;
  font-family: Oswald;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  background-color: $bg-grey;
  padding: 10px 0;
}
