@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-display: swap;
}
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.page__body {
  margin: 0;
  background-color: #a2acab;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-body {
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  background-color: #ffffff;
  color: #000000;
}

.button {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 8px 44px;
  text-decoration: none;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  border: none;
  background-color: #68b738;
  color: #ffffff;
  margin: 0;
  box-sizing: border-box;
  text-align: center;
}

.button--primary {
  font-size: 16px;
  line-height: 20px;
  padding: 10px 0;
}

.button--secondary {
  color: #444444;
  font-family: Oswald;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  background-color: #F2F2F2;
  padding: 10px 0;
}

.main-header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 11px 20px;
  margin: 0 auto;
  text-align: center;
  position: relative;
  align-items: center;
  width: 320px;
  box-sizing: border-box;
  background-color: #ffffff;
}

.main-nav--close {
  display: none;
}

.main-nav__list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  width: 320px;
  margin: 0 auto;
  text-align: center;
  z-index: 1;
}

.main-nav__link {
  color: #000000;
  font-family: "Oswald", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
}

.main-nav__item {
  padding: 20px 0;
  box-sizing: border-box;
  border-top: 1px solid #e6e6e6;
}

.hero {
  background-color: #68B738D9;
}

.hero__container {
  padding: 27px 20px 0 20px;
  background-image: url("../img/hero/hero-background.jpg");
  background-position: top;
  background-repeat: no-repeat;
  width: 100%;
  width: 320px;
  margin: 0 auto;
  margin-bottom: 20px;
}

.hero__content {
  display: flex;
  flex-direction: column;
  color: #ffffff;
  text-align: center;
  align-items: center;
}

.hero__title {
  width: 100%;
  margin: 0;
  color: #ffffff;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  margin-bottom: 27px;
  font-family: "Oswald", sans-serif;
}

.hero__description {
  margin: 0;
  font-family: Oswald;
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  text-transform: uppercase;
  margin-bottom: 29px;
}

.hero__link {
  min-height: 40px;
  min-width: 280px;
  align-items: center;
}

.logo__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.programs__wrapper {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 20px;
}

.programs__list {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  gap: 20px;
  margin: 0;
  padding: 0;
}

.programs {
  background-color: #ffffff;
}

.program-card {
  background-color: #F2F2F2;
  position: relative;
  padding: 30px 20px 14px 20px;
}

.program-card__title {
  margin: 0;
  color: #000000;
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 37px;
  text-transform: uppercase;
  margin-bottom: 29px;
}

.program-card__descr {
  color: #444444;
  margin: 0;
  font-family: "Lato", sans-serif;
  font-size: 14px;
  line-height: 18px;
  margin-bottom: 42px;
  font-weight: 400;
}

.program-card__link {
  color: #000000;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  text-transform: uppercase;
  text-decoration: none;
}

.advantages__wrapper {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 49px;
}

.advantages__title {
  margin: 0;
  color: #000000;
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 40px;
  margin-bottom: 40px;
}

.advantages__list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  gap: 20px;
}

.advantages__item {
  color: #444444;
  font-family: "Lato", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  margin-left: 80px;
}

.advantages {
  background-color: #ffffff;
}

.example {
  background-color: #EAEAEA;
}

.example__container {
  width: 320px;
  margin: 0 auto;
  padding: 25px 20px 40px 20px;
  box-sizing: border-box;
}

.example__title {
  color: #000000;
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 40px;
  margin-bottom: 40px;
}

.example__desription {
  color: #444444;
  font-family: "Lato", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  margin-bottom: 20px;
}

.example__list {
  display: flex;
  gap: 32px;
  margin-bottom: 20px;
}

.example__item {
  margin: 0 auto;
  padding-top: 14px;
}

.example__number {
  color: #000000;
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  text-align: center;
  margin-bottom: 7px;
}

.example__descr {
  color: #444444;
  font-family: "Lato", sans-serif;
  font-size: 12px;
  font-weight: 400;
  text-align: center;
  line-height: 12px;
}

.example__legend {
  color: #000000;
  text-align: center;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.map__wrapper {
  width: 320px;
  margin: 0 auto;
  padding: 26px 20px;
  display: grid;
  grid-template-columns: repeat(2, 140px);
  align-items: center;
  box-sizing: border-box;
  background-color: #ffffff;
}

.map__title {
  color: #111111;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0;
}

.map__descr {
  color: #444444;
  font-family: "Lato", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  margin: 0;
  font-style: normal;
}

.map__link {
  background-image: url("../img/map/location.png");
  width: 320px;
  height: 362px;
}

.map__wrapper--catalog {
  background-color: #F2F2F2;
}

.main-footer {
  display: flex;
  padding: 20px;
  box-sizing: border-box;
  background-color: #F2F2F2;
}

.main-footer__container {
  width: 280px;
  margin: 0 auto;
}

.main-footer__logo {
  display: flex;
  justify-content: center;
  padding-bottom: 20px;
  border-bottom: 1px solid #D9D9D9;
  box-sizing: border-box;
}

.main-footer__developer {
  margin: 0;
  padding-top: 20px;
  box-sizing: border-box;
}

.social__list {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 24px;
  margin: 0;
  box-sizing: border-box;
  justify-content: center;
  padding: 20px 0;
  border-bottom: 1px solid #D9D9D9;
}

.developer__link {
  text-decoration: none;
  display: flex;
  justify-content: space-between;
}

.developer__text {
  color: #444444;
  font-family: "Lato", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  margin: 0;
}

.products__container {
  width: 320px;
  margin: 0 auto;
  background-color: #ffffff;
}

.products__title {
  color: #000000;
  font-family: "Oswald", sans-serif;
  margin: 0;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  padding: 28px 0px 41px 20px;
  border-top: 1px solid #EBEBEB;
  border-bottom: 1px solid #EBEBEB;
  box-sizing: border-box;
}

.products__list {
  list-style-type: none;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0;
}

.products__item {
  width: 320px;
  border-bottom: 1px solid #EBEBEB;
}

.product-card {
  padding: 20px 20px 24px 20px;
  box-sizing: border-box;
  background-color: #ffffff;
}

.product-card__title {
  color: #222222;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 14px;
}

.product-card__wrapper {
  display: flex;
  margin: 0;
  margin-bottom: 17px;
}

.product-card__composition__wrapper {
  display: flex;
  justify-content: space-between;
  color: #444444;
  font-family: "Lato", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  padding: 0;
  margin: 0;
  width: 140px;
}

.product-card__desc-wrapper {
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
}

.product-card__button {
  width: 100%;
}

.product-card__composition {
  margin: 0;
  padding: 0;
}

.product-card__title--show-more {
  color: #111111;
  margin-bottom: 8px;
  text-align: center;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
}

.product-card__wrapper--show-more {
  flex-wrap: wrap;
  margin: 0 auto;
  justify-content: center;
}

.product-card__description--show-more {
  display: block;
  margin: 0;
  color: #444444;
  text-align: center;
  font-family: Lato;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  margin-bottom: 21px;
}

.product-card__image {
  display: block;
  margin: 0;
}

.product-card__image--show-more {
  background-image: url("../img/catalog/plus.svg");
  width: 30px;
  height: 30px;
  display: block;
  margin: 0 auto;
  margin-bottom: 23px;
}

.product-card--show-more {
  padding: 25px 20px;
}

.additionally-products__container {
  width: 320px;
  margin: 0 auto;
  padding-top: 25px;
  padding-left: 20px;
  background-color: #ffffff;
  box-sizing: border-box;
}

.additionally-products__list {
  list-style-type: none;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  padding-right: 20px;
}

.additionally-products__title {
  color: #000000;
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  margin: 0;
  padding-bottom: 36px;
  box-sizing: border-box;
}

.additionally-product__title {
  color: #222222;
  margin: 0;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  text-transform: uppercase;
  margin-bottom: 13px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  width: 280px;
}

.additionally-product__button {
  width: 100%;
}

.additionally-product__item {
  border-bottom: 1px solid #CDCDCD;
  padding: 13px 0 19px 0;
  width: 280px;
}

.additionally-product__item:first-child {
  border-top: 1px solid #CDCDCD;
}

.additionally-product__quantity {
  color: #444444;
  font-family: "Lato", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  margin: 0;
}

.additionally-product__price {
  color: #444444;
  font-family: "Lato", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 14px;
  margin: 0;
}

.additionally-product__wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 14px;
}

.gift-container {
  width: 320px;
  padding: 35px 20px;
  box-sizing: border-box;
  margin: 0 auto;
  background-color: #ffffff;
}

.gift__wrapper {
  background-image: url("../img/catalog/gifts-background.png");
  background-repeat: no-repeat;
  background-color: #68B738D9;
  background-size: contain;
  box-sizing: border-box;
  padding: 58px 61px 42px 58px;
  flex-wrap: wrap;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.gift__title {
  color: #ffffff;
  text-align: center;
  font-family: "Lato", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  margin: 0;
}

.gift {
  display: flex;
  margin: 0;
  box-sizing: border-box;
}

.gift__icon {
  margin-bottom: 48px;
}

.program__selection__container {
  width: 320px;
  margin: 0 auto;
  background-color: #ffffff;
}

.program-selection__title {
  color: #000000;
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  margin: 0;
  margin-bottom: 37px;
}

.program-selection__description {
  color: #000000;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  margin: 0;
}

.program-selection__title-wrapper {
  padding: 27px 29px 39px 20px;
  box-sizing: border-box;
  border-top: 1px solid #D9D9D9;
}

.program-selection__wrapper-user {
  margin: 0;
  padding: 26px 20px 24px 20px;
  box-sizing: border-box;
}

.form__label {
  color: #444444;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
}

.form__contain {
  padding: 0 20px 24px;
  box-sizing: border-box;
  border-bottom: 1px solid #D9D9D9;
  flex-direction: column;
}

.form__wrapper {
  display: flex;
  gap: 13px;
  align-items: center;
  margin: 0;
  margin-bottom: 14px;
}

.form-control {
  color: #444444;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
  padding: 13px 14px 6px 14px;
  box-sizing: border-box;
  flex-grow: 1;
  border: 1px solid #E7E7E7;
}

.form__title {
  color: #000000;
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 26px;
  margin: 0;
  margin-bottom: 35px;
}

.form__radio {
  border-bottom: 1px solid #D9D9D9;
}

.form-control--text-area {
  width: 100%;
  min-height: 150px;
}

.radio-target {
  border: none;
  box-sizing: border-box;
  margin: 0;
}

.radio-target__list {
  list-style-type: none;
  margin: 0;
  border-bottom: 1px solid #D9D9D9;
  padding: 22px 20px 0 20px;
}

.radio-target__control-label {
  color: #444444;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
}

.radio-target__item {
  margin-bottom: 25px;
}